import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Title } from '@angular/platform-browser';
import { Router, ActivatedRoute } from "@angular/router";
import { ToastrService } from 'ngx-toastr';
import { PortalAPIService } from 'src/app/shared/service/portal-api.service';
import { RoleEnum } from 'src/app/shared/service/portal.model';
import { environment } from 'src/environments/environment';


@Component({
  selector: 'app-sign-in',
  templateUrl: './sign-in.component.html',
  styleUrls: ['./sign-in.component.scss']
})
export class SignInComponent implements OnInit {
  public loginForm: FormGroup;
  spinner: boolean;

  constructor(private router: Router, private route: ActivatedRoute,
    private apiService: PortalAPIService,
    public toastr: ToastrService, titleService: Title,
    private formBuilder: FormBuilder) {
    this.createLoginForm();
    titleService.setTitle(`Login | ${environment.appName}`);
  }

  createLoginForm() {
    this.loginForm = this.formBuilder.group({
      email: [''],
      password: [''],
    })
  }

  // On Forgotpassword link click
  onForgotpassword() {
    this.router.navigate(['forgot-password'], { relativeTo: this.route.parent });
  }

  // On Signup link click
  onSignup() {
    this.router.navigate(['sign-up'], { relativeTo: this.route.parent });
  }


  ngOnInit(): void {
  }

  onSubmit() {
    this.spinner = true;
    if (this.loginForm.value.password && this.loginForm.value.email) {
      let req = {
        password: this.loginForm.value.password,
        email: this.loginForm.value.email,
      };

      this.apiService.post(`user/login`, req).subscribe(res => {
        if (res && res.status === 200) {
          let userDetails = res.data;
          userDetails.token = res.token;

          let cipher = this.apiService.encrypt(userDetails);
          sessionStorage.setItem(environment.localStoreKey, cipher);
          this.spinner = false;

          this.roleBased(userDetails);
        }
        else if (res && res.status === 400) {
          this.toastr.error(res.message, '');
          this.spinner = false;
        }
      });

    }
    else {
      this.toastr.error("Please Enter the Required Fields", "");
      this.spinner = false;
    }
  }

  roleBased(userDetails) {
    if (userDetails.role == RoleEnum.ADMIN)
      this.router.navigateByUrl("/dashboard/analytics");
  }

}
