import { Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { Router, ActivatedRoute } from "@angular/router";
import { ToastrService } from 'ngx-toastr';
import { PortalAPIService } from 'src/app/shared/service/portal-api.service';
import { RoleEnum } from 'src/app/shared/service/portal.model';
import { environment } from 'src/environments/environment';


@Component({
  selector: 'app-sign-up',
  templateUrl: './sign-up.component.html',
  styleUrls: ['./sign-up.component.scss']
})
export class SignUpComponent implements OnInit {
  spinner: boolean;
  formDetails: any = {
    fName: '',
    lName: '',
    password: '',
    email: '',
    phone: '',
    companyName: '',
    role: RoleEnum.Customer,
    zipCode: null,
    country: (new Date()).toString().split('(')[1].split(" ")[0],
    terms: false
  }

  constructor(private router: Router, private route: ActivatedRoute,
    private apiService: PortalAPIService,
    public toastr: ToastrService, titleService: Title) {
    titleService.setTitle(`Signup | ${environment.appName}`);
  }

  // On Signup link click
  onSignIn() {
    this.router.navigate(['sign-in'], { relativeTo: this.route.parent });
  }

  ngOnInit(): void {
  }

  onSubmit() {
    if (this.formDetails.terms === false) {
      this.toastr.warning("Please agree to Terms & Conditions")
      return
    }

    this.apiService.post(`user`, this.formDetails).subscribe(res => {
      if (res && res.status === 201) {

        this.apiService.post(`aws/sendEmail`, { recipient: this.formDetails.email }).subscribe(resEmail => {
          this.toastr.success('OTP has been sent to your mail for verification.');
        });
      }
      else if (res && res.status === 400) {
        this.toastr.error(res.message, '');
        this.spinner = false;
      }
    });

  }

}
