import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';

import * as FileSaver from 'file-saver';
import { Observable } from 'rxjs';

import * as XLSX from 'xlsx';
import { environment } from '../../../environments/environment';
const EXCEL_TYPE = 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8';
const EXCEL_EXTENSION = '.xlsx';
import * as CryptoJS from 'crypto-js';

@Injectable({
  providedIn: 'root'
})

export class PortalAPIService {
  baseUrl: string;
  token: string;
  siteNameList: any[];
  monthNames: any = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];

  constructor(
    private http: HttpClient
  ) {
    this.baseUrl = environment.baseUrl;
  }

  getHeaders(headers: { key: string, value: string }[] = null): HttpHeaders {
    let user = this.getUserInfo();
    if (user !== null)
      this.token = user.token;

    var headerCollection = new HttpHeaders(
      {
        'Authorization': 'Bearer ' + this.token,
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin': '*'
      });

    if (headers) {
      headers.forEach(header => {
        headerCollection.append(header.key, header.value);
      });
    }
    return headerCollection;
  }


  getQueryParams(queryParams: { key: string, value: string }[] = null): URLSearchParams {
    var search = new URLSearchParams();
    if (queryParams) {
      queryParams.forEach(item => {
        search.set(item.key, item.value);
      })
    }
    return search;
  }

  get(url: string): Observable<any> {
    return this.http.get(this.baseUrl + url, { headers: this.getHeaders() });
  }

  post(url: string, data: any, type?: string): Observable<any> {
    return this.http.post(this.baseUrl + url, JSON.stringify(data), { headers: this.getHeaders() });
  }

  put(url: string, data: any): Observable<any> {
    var putData = JSON.stringify(data);
    return this.http.put(this.baseUrl + url, putData, { headers: this.getHeaders() });
  }

  delete(url: string): Observable<any> {
    return this.http.delete(this.baseUrl + url, { headers: this.getHeaders() });
  }

  getUserInfo() {
    let user = this.decrypt(sessionStorage.getItem(environment.localStoreKey));
    return user;
  }



  getMonthYear(date: any) {
    var d = new Date(date);
    return `${this.monthNames[d.getMonth()]} ${d.getFullYear()}`
  }

  getPreviousMonthYear(date: any) {
    var d = new Date(date);
    d.setMonth(d.getMonth() - 1);
    return `${this.monthNames[d.getMonth()]} ${d.getFullYear()}`
  }

  getCurrentMonthYear() {
    let list = this.monthNames.map(x => (x + ' ' + new Date().getFullYear()))
    return list;
  }


  public exportAsExcelFile(json: any[], excelFileName: string, SheetName: string = 'data') {
    const worksheet: XLSX.WorkSheet = XLSX.utils.json_to_sheet(json);
    const sheets = {};
    sheets[SheetName] = worksheet

    const workbook: XLSX.WorkBook = { Sheets: sheets, SheetNames: [SheetName.toString()] };
    const excelBuffer: any = XLSX.write(workbook, { bookType: 'xlsx', type: 'array' });
    const data: Blob = new Blob([excelBuffer], { type: EXCEL_TYPE });
    FileSaver.saveAs(data, excelFileName + '_' + new Date().getTime() + EXCEL_EXTENSION);
    return true;
  }


  getListById(list, id) {
    return list?.find(item => item?._id == id);
  }

  dateDiff(date1, date2) {
    let diffValue = 0;
    if (date1 && date2) {
      var d1 = new Date(date1);
      var d2 = new Date(date2);
      var diff = d2.getTime() - d1.getTime();
      diffValue = diff / (1000 * 60 * 60 * 24);
    }
    return diffValue;
  }



  formatDate(date) {
    if (date == undefined || date == null || date == '') {
      return null;
    }
    else {
      try {
        let orderDate;
        let cDate = date.trim();
        orderDate = cDate.replace(/-/g, '/');
        orderDate = orderDate.split("/").reverse().join("-");
        return new Date(orderDate).toISOString();
      }
      catch (e) {
        console.log(e, date);
        return null;
      }
    }
  }

  formatISODate(date) {
    if (date !== null) {
      return new Date(date).toLocaleDateString('en-GB', {
        month: '2-digit', day: '2-digit', year: 'numeric'
      });
    }
  }
  


  filterUnique(array: any, keyProps: any[]) {
    const kvArray = array.map(entry => {
      const key = keyProps.map(k => entry[k]).join('|');
      return [key, entry];
    });
    const map = new Map(kvArray);
    return Array.from(map.values());

  }


  /* method is used to encrypt and decrypt the text  */
  encrypt(data: any) {
    let cipher = CryptoJS.AES.encrypt(JSON.stringify(data), environment.secretKey).toString();
    return cipher;
  }

  decrypt(ciphertext: string) {
    let decryptedData = null;
    if (ciphertext != undefined) {
      let bytes = CryptoJS.AES.decrypt(ciphertext, environment.secretKey);
      decryptedData = JSON.parse(bytes.toString(CryptoJS.enc.Utf8));
    }
    return decryptedData;
  }

}

