import { Component, OnInit } from '@angular/core';
import { SidebarService } from '../sidebar/sidebar.service';
import { Location } from '@angular/common';
import { Router } from '@angular/router';
import { environment } from 'src/environments/environment';
import { PortalAPIService } from '../service/portal-api.service';


@Component({
    selector: 'app-navbar',
    templateUrl: './navbar.component.html',
    styleUrls: ['./navbar.component.scss']
})

export class NavbarComponent implements OnInit {

    route: string;
    analytics: any;
    userDetails: any;

    constructor(private apiService: PortalAPIService,
        public sidebarservice: SidebarService, location: Location, public router: Router) {
        let user = this.apiService.getUserInfo();
        if (user) {
            this.userDetails = user;
        }

        router.events.subscribe((val) => {
            if (location.path() == '/dashboard/analytics') {
                $('html').removeAttr('class')
                this.analytics = true;
            } else {
                this.analytics = false;
                if (location.path() == '/dashboard/e-commerce') {
                    $('html').removeAttr('class')
                    $('html').addClass('color-sidebar sidebarcolor3 color-header headercolor1')
                } else {
                    if (location.path() == '/dashboard/alternate') {
                        $('html').removeAttr('class')
                        $('html').addClass('color-sidebar sidebarcolor3')
                    } else {
                        if (location.path() == '/dashboard/hospitality') {
                            $('html').removeAttr('class')
                            $('html').addClass('color-sidebar sidebarcolor3 color-header headercolor5')
                        }

                    }
                }
            }
        });

    }

    toggleSidebar() {
        this.sidebarservice.setSidebarState(!this.sidebarservice.getSidebarState());
    }

    getSideBarState() {
        return this.sidebarservice.getSidebarState();
    }

    hideSidebar() {
        this.sidebarservice.setSidebarState(true);
    }

    ngOnInit() {

        /* Search Bar */
        $(document).ready(function () {
            $(".mobile-search-icon").on("click", function () {
                $(".search-bar").addClass("full-search-bar")
            }),
                $(".search-close").on("click", function () {
                    $(".search-bar").removeClass("full-search-bar")
                })
        });

    }

    logout() {
        sessionStorage.removeItem(environment.localStoreKey);
        sessionStorage.removeItem(environment.localStoreLoginKey);
        this.router.navigateByUrl("/");
        window.location.reload();
    }
}
